import React, {Component} from 'react';
import {CategoryItem} from "./CatogoryItem";

class Categories extends Component {
    categories = [
        {
            image:"https://images.unsplash.com/photo-1596461404969-9ae70f2830c1",
            label: "Toys"
        },
        {
            image:"https://images.unsplash.com/photo-1519689680058-324335c77eba",
            label: "Baby Cloths"
        },
        {
            image:"https://images.unsplash.com/photo-1610890716171-6b1bb98ffd09",
            label: "Board Games"
        },
        {
            image:"https://images.unsplash.com/photo-1579952363873-27f3bade9f55",
            label: "Sports"
        }
    ]
    render() {
        return (
            <div className={"shadow d-flex justify-content-center item-align-center"} >
                {
                    this.categories.map((category, index) => {
                      return  <CategoryItem key={"category_index_" + index} category={category} />;
                    })
                }
            </div>
        );
    }
}

export default Categories;