import React, {Component} from 'react';
import {Carousel} from "react-bootstrap";
import {Link} from "react-router-dom";

class CarouselPanel extends Component {
    render() {
        return (
            <Carousel controls={false} interval={5000} indicators={false}>
                {
                    this.props.items &&
                    this.props.items.map( (item, index) => {
                        return <Carousel.Item key={"carousel_item_" + index} className={"no-text-decor"} as={Link} to={item.link}>
                            <div style={{height: "46vw"}} className={""}>
                                {item.component}
                            </div>
                        </Carousel.Item>
                    })
                }
            </Carousel>
        );
    }
}

export default CarouselPanel;