import React, {Component} from 'react';
import {Button, Col, Container, Form, FormControl, Row} from "react-bootstrap";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";

class SellBicycleNow extends Component {
    state = {
        selected_images: []
    }

    constructor(props) {
        super(props);
        this.onChangeImage = this.onChangeImage.bind(this);
    }

    onChangeImage(event) {
        if(event.target.files.length  <= 0 ) {
            return;
        }

        this.setState({
            selected_images: [...this.state.selected_images, URL.createObjectURL(event.target.files[0])]
        });
    }

    render() {
        return (
            <section>
                <Container className={"py-5"}>
                    <div>
                        <h4> SELL BICYCLE </h4>
                    </div>
                    <Row className={"g-0"}>
                        <Col className={"p-1"}>
                            <label>
                                Select Brand
                            </label>
                            <Form.Select>
                                <option value="1">Firefox</option>
                                <option value="2">Hero</option>
                                <option value="3">Atlas</option>
                            </Form.Select>
                        </Col>
                        <Col className={"p-1"}>
                            <label>
                                Select Model
                            </label>
                            <Form.Select>
                                <option value="1">Firefox 01</option>
                                <option value="2">Firefox 02</option>
                                <option value="3">Firefox 03</option>
                            </Form.Select>
                        </Col>
                    </Row>
                    <div className={"p-1"}>
                        <label>
                            Add images
                        </label>
                        <div className={"bg-white d-flex"} style={{height: 300, overflowX: "auto"}}>
                            {
                                this.state.selected_images.map((image, index) => {
                                    return <div className={"p-1 h-100 position-relative"}>
                                        <img style={{minWidth:"100%"}} src={image} alt={"selected_im_" + index} className={"h-100"} />
                                        <Button variant={"danger"} className={"position-absolute rounded-circle fw-bold"} style={{top:10, right: 10}}>
                                            <FontAwesomeIcon icon={"trash"} />
                                        </Button>
                                    </div>;
                                })
                            }
                        </div>
                        <FormControl type={"file"} ref={(ref) => this.uploadImage = ref} id={"img-file-input"}  hidden={true} onChange={this.onChangeImage} />
                        <Button variant={"success"} className={"rounded-circle m-1"} onClick={()=> {
                            this.uploadImage.click();
                        }}>
                            <FontAwesomeIcon icon={"plus"} />
                        </Button>
                        Click here to add new image
                    </div>
                    <div className={"p-1"}>
                        Estimate price is: Rs. 12434 (This one is not the final price, price may change after inspection)
                    </div>
                    <div className={"mt-4"}>
                        <Button variant={"primary m-1"} >
                            Sell Now
                        </Button>
                        <Button variant={"primary m-1"} >
                            Sell Later
                        </Button>
                    </div>
                </Container>
            </section>
        );
    }
}

export default SellBicycleNow;