import React, {Component} from 'react';
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import {
    ACCOUNT_URL,
    BUY_NOW_PAGE_URL,
    HOME_URL,
    LOGIN_PAGE_URL,
    SELL_NOW_PAGE_URL
} from "../../constants/application/ApplicationUrl";
import {Link} from "react-router-dom";
import "./bottomnav.css";

class BottomNavs extends Component {
    constructor(props) {
        super(props);
        this.Tab = this.Tab.bind(this);
    }

    state = {
        tabs: [
            {
                name: "Home",
                icon: "home",
                link: HOME_URL
            },
            {
                name: "Buy",
                icon: "bicycle",
                link: BUY_NOW_PAGE_URL
            },
            {
                name: "Sell",
                icon: "laptop",
                link: SELL_NOW_PAGE_URL
            },
            {
                name: "Account",
                icon: "user",
                link: this.login? ACCOUNT_URL: LOGIN_PAGE_URL
            }
        ]
    }

    Tab({name, icon, link}) {
       return <Link to={link} className={"no-text-decor"}><div className={`d-flex flex-column justify-content-center text-center p-2 ${link === window.location.pathname && "text-primary"}`}>
            <FontAwesomeIcon icon={icon} size={"lg"} />
            <b>{name}</b>
        </div></Link>;
    }

    render() {
        return (
            <section className={"position-absolute bottom-0 w-100 bg-white position-fixed px-4 bottom-nav"}>
                {
                }
                <div className={"d-flex justify-content-between"}>
                    {
                        this.state.tabs.map((nav, index) => {
                            return <this.Tab {...nav} key={"tab_" + index} />
                        })
                    }
                </div>
            </section>
        );
    }
}

export default BottomNavs;