import React from 'react';
import ReactDOM from 'react-dom';
import reportWebVitals from './tests/reportWebVitals';

import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css';
import App from './main/pixiemart/components/app/App';

import { BrowserRouter } from "react-router-dom";
import { Provider } from 'react-redux';
import {store, persist_store} from './redux/store';
import { PersistGate } from 'redux-persist/integration/react';
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab } from '@fortawesome/free-brands-svg-icons';
import {
    faUser,
    faCoffee,
    faAddressBook,
    faLocationArrow,
    faSearchLocation,
    faEdit,
    faTrash,
    faSearch,
    faAddressCard,
    faBox,
    faBoxOpen,
    faUserTie,
    faUserLock,
    faRupeeSign,
    faPlus, faHome, faBicycle, faLaptop, faTruck, faLeaf, faCreditCard, faBalanceScale, faCogs
} from '@fortawesome/free-solid-svg-icons';

library.add(
    fab,
    faUser,
    faCoffee,
    faAddressBook,
    faLocationArrow,
    faSearchLocation,
    faEdit,
    faTrash,
    faSearch,
    faAddressCard,
    faBox,
    faBoxOpen,
    faUserTie,
    faUserLock,
    faRupeeSign,
    faPlus,
    faHome,
    faBicycle,
    faLaptop,
    faTruck,
    faLeaf,
    faCreditCard,
    faBalanceScale,
    faCogs
)

ReactDOM.render(
    <React.StrictMode>
        <Provider store={store}>
            <PersistGate loading={null} persistor={persist_store}>
                <BrowserRouter/>
                <App/>
            </PersistGate>
        </Provider>
    </React.StrictMode>,
    document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
